import Header from './components/Header'
import Form from './components/Form'

const phi = 1.618033988749895

// Uses the fibonacci formula to find the number of ways to get to the top of n stairs
// http://www.maths.surrey.ac.uk/hosted-sites/R.Knott/Fibonacci/fibFormula.html
let climbingStairs = (numStairs) => {
  return Math.round( (Math.pow(phi, numStairs+1) - Math.pow(1-phi, numStairs+1)) / Math.sqrt(5))
}


function App() {

  return (
    <div className="App">
      <Header title='Number of Stairs'/>
      <Form onSubmit={climbingStairs}/>
    </div>
  );

  
  
}

export default App;
