import { useState } from 'react';

const Form = ({onSubmit}) => {

    const [numStairs, setNumStairs] = useState(0)
    const [result, setResult] = useState('')

    const handleSubmit = e => {
        e.preventDefault()
        
        setResult(onSubmit(Number(e.target[0].value)))
        console.log(result)
    }


    return (
        <div>
            <form onSubmit={handleSubmit} className='form'>
                <input type='number' value={numStairs} onChange={e => setNumStairs(e.target.value)} min='0' className='field' />
                <input type='submit' value='Calculate' className='btn'/>
            </form>
            <h2>{result}</h2>
        </div>
    );
}

export default Form
